html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  font-weight: 400;
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);

  @include small-tablet{
    margin: 0;
    padding: 0;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.main {
  &__title{
    margin: 0;
    font-weight: 700;
    font-size: 64px;
    line-height: 130%;
    letter-spacing: 1px;
    color: var(--dark-gray-color);

    &_home{
      color: var(--white-color);
      margin-bottom: 20px;
    }

    @include tablet{
      font-size: 46px;
    }
  }

  &__subtitle{
    margin: 0;
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 46px;
    line-height: 130%;
    color:var(--dark-gray-color);
    &_center{
      text-align: center;
    }

    @include tablet{
      font-size: 32px;
    }
  }

  &__headline{
    margin: 0;
    margin-bottom: 35px;

    font-weight: 700;
    font-size: 28px;
    line-height: 150%;
    color: var(--dark-gray-color);

    &_center{
      text-align: center;
    }

    @include tablet{
      font-size: 24px;
    }
  }

  &__cardtitle{
    margin: 0;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: var(--dark-gray-color);

    &_center{
      text-align: center;
    }

    @include tablet{
      font-size: 18px;
    }
  }

  &__descr{
    margin: 0;
    font-weight: 400;
    font-size: 20px;
    line-height: 150%;
    color: var(--storm-gray-color);

    &_home{
      color: var(--white-color);
      margin-bottom: 61px;
    }

    @include tablet{
      font-size: 16px;
    }
  }

  &__text{
    margin: 0;
    margin-bottom: 60px;
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    color: var(--storm-gray-color);

    &_center{
      text-align: center;
    }
  }

  &__subtext{
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    color: var(--storm-gray-color);
    &_center{
      text-align: center;
    }

    @include tablet{
      font-size: 14px;
    }
  }

  &__minitext{
    margin: 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: var(--dark-gray-color);
    &_center{
      text-align: center;
    }

    @include tablet{
      font-size: 12px;
    }
  }

  &__link{
    color: var(--dark-gray-color);
    transition: color 0.3s ease-in-out;

    &:hover{
      color: var(--ten-color);
    }
  }
}

.section_offset{
  padding-top: 120px;
}

.btn{
  align-items: center;
  padding: 0px 40px;
  border-style: solid;
  border-radius: 4px;
  border-width: 1px;
  border-color: transparent;

  font-weight: 700;
  font-size: 16px;
  line-height: 52px;

  color: var(--white-color);

  display: inline-block;
  align-items: center;
  text-align: center;

  transition: background-color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;

  &_fill{
    color: var(--white-color);
    background-color: var(--ten-color);

    &:hover{
      background-color: var(--primary-color);
    }
  }

  &_stroke{
    border-color: var(--white-color);

    &:hover{
      box-shadow: 0 0 8px white;
    }

    &-primary{
      border-color: var(--ten-color);
      color: var(--ten-color);

      &:hover{
        box-shadow: 0 0 8px var(--ten-color);
      }
    }
  }
}

.slider-nav{
  display: flex;

  &__btn{
    display: flex;
    align-items: center;
    border-radius: 100%;
    padding: 18px 15px;
    transition: background-color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;

    & img{
      width: 18px;
      height: 11px;
    }

    &_prev{
      margin-right: 5px;
      &:hover{
        box-shadow: 0px 0px 4px rgb(149, 140, 140);
      }
    }

    &_next{
      background-color: var(--ten-color);
      &:hover{
        background-color: var(--primary-color);
      }
    }

    @include mobile{
      display: none;
    }
  }
}

.learn-more{
  display: flex;
  justify-content: center;
  align-items: center;

  &__headline{
    margin: 0;
    margin-right: 40px;
  }

  @include small-tablet{
    & *{
      margin-right: 10px !important;
    }
  }

  @include mobile{
    flex-direction: column;
  }
}

.form-field {
  display: flex;
  flex-direction: column;


  &__caption {
    margin-bottom: 7px;
    font-size: 14px;
    line-height: 150%;
    color: var(--gray-color);
  }

  &__input {
    border: 1px solid #d7dadd;
    border-radius: 4px;
    padding-left: 15px;
    width: 260px;
    height: 44px;
    font-size: 14px;
    line-height: 150%;

    &::placeholder {
      color: #9a9ca5;
    }

    &:focus {
      outline: 2px solid var(--primary-color);
    }

    &_big {
      padding-top: 10px;
      height: 66px;
      resize: none;
    }
  }

  &_big {
    flex-grow: 1;

    .form-field__input {
      width: 100%;
    }
  }

  &_textarea{
    min-width: 100%;
    max-width: 100%;
    min-height: 140px;
  }

  &_gray {
    width: 100%;

    .form-field__input {
      width: 100%;
      background-color: var(--athens-color);
    }
  }

  &__select{
    border: 1px solid #d7dadd;
    border-radius: 4px;
    padding-left: 15px;
    padding-right: 15px;
    width: 100%;
    height: 44px;
    font-size: 14px;
    line-height: 150%;
    color: var(--dark-gray-color);

    &:focus {
      outline: 2px solid var(--primary-color);
    }

    &_big {
      padding-top: 10px;
      height: 66px;
      resize: none;
    }
  }
}

.hero{
  &__content{
    max-width: 600px;
    padding: 20px;
    background-color: var(--bg-oth-color);
  }

  &-wrapper{
    width: 100%;
    height: 100%;
    padding-top: var(--padding-top-title);
    padding-bottom: 200px;
    background-color: var(--bg-wrapper-color);
  }
}

.burger {
  --burger-width: 40px;
  --burger-height: 20px;
  --burger-line-height: 2px;

  position: absolute;
  right: 15px;
  top: 23px;
  z-index: 1000;
  display: none;
  border: none;
  padding: 0;
  width: var(--burger-width);
  height: var(--burger-height);
  color: #000;
  background-color: transparent;
  cursor: pointer;

  @include tablet {
    display: block;
  }
}

.burger::before,
.burger::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}

.burger::before {
  top: 0;
}

.burger::after {
  top: calc(100% - var(--burger-line-height));
}

.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transform: translateY(-50%);
  transition: transform 0.3s ease-in-out;
}

.burger--active::before {
  top: 50%;
  transform: rotate(45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}

.burger--active::after {
  top: 50%;
  transform: rotate(-45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}

.burger--active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}

.burger:focus::after {
  opacity: 1;
}

.overlay {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 500;
  background-color: rgba(#000, 0.6);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

  &--active {
    opacity: 1;
    visibility: visible;
  }
}
