.price-list{
  padding-top: 20px;
  padding-bottom: 30px;
  background-color: var(--bg-gray-color);
}

.table-wrapper{
  position: relative;
  z-index: 5;
  overflow: hidden;
  border: 1px solid #e5e8ed;
  border-collapse: collapse;
  border-radius: 4px;
  width: 100%;
  box-shadow: var(--main-shadow);
  background-color: var(--color-light);

  @include tablet {
    overflow-x: auto;
  }
}

.table{
  border-collapse: collapse;
  width: 100%;
  text-align: center;
  box-shadow: var(--main-shadow);
  background-color: var(--white-color);

  @include tablet{
    width: 1200px;
  }

  &__th{
    padding-top: 30px;
    padding-bottom: 20px;
    &:first-child{
      vertical-align: bottom;
      padding-left: 24px;
      font-weight: 700;
      font-size: 18px;
      line-height: 150%;
      text-align: left;
      color: var(--darkr-gray-color);
    }
  }

  &__headline{
    font-weight: 700;
    font-size: 24px;
    line-height: 150%;
    color: var(--darkr-gray-color);
    text-align: center;
  }

  &__price{
    text-align: center;
    line-height: 150%;
    font-weight: 700;
    font-size: 18px;
    line-height: 150%;
    color: var(--storm-gray-color);
  }

  &__body{
    .table__row{
      &:nth-child(odd){
        background-color: var(--bg-gray-color);
      }
    }
  }


  &__col{
    padding: 26px 0;

    &:first-child{
      vertical-align: bottom;
      padding-left: 24px;
      font-size: 18px;
      line-height: 150%;
      text-align: left;
      color: var(--darkr-gray-color);
    }
  }


}
