.contact-hero{
  position: relative;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url('../img/contacts/bg.jpg');

  &__content{
    max-width: 600px;
    padding: 20px;
    background-color: var(--bg-oth-color);
  }

  &__title{
    margin-bottom: 24px;
  }
}

.contact-wrapper{
  width: 100%;
  height: 100%;
  padding-top: 143px;
  padding-bottom: 200px;
  background-color: var(--bg-wrapper-color);
}
