.about-main{
  padding-top: 120px;
  padding-bottom: 120px;
  &__container{
    display: flex;
    align-items: flex-start;

    @include tablet {
      display: block;
    }
  }

  &__img-container{
    border-radius: 4px;
  }

  &__img{
    border-radius: 4px;
  }

  &__content{
    position: relative;
    left: -30px;
    top: -130px;
    z-index: 10;
    padding: 80px 82px;
    padding-right: 105px;
    padding-left: 135px;
    padding-bottom: 60px;
    max-width: 735px;

    box-shadow: var(--main-shadow);
    background: white;

    @include minidesk {
      left: 0;
      top: 0;
      width: 55%;
    }

    @include tablet {
      width: 100%;
    }

    @include mobile {
      padding: 15px;
    }
  }

  &__text{
    margin-bottom: 30px;
  }

  &__subtext{
    margin-bottom: 60px;
  }
}


.quote{
  &__author{
    display: block;
    color: var(--dark-gray-color);
  }

  &__post{
    color: var(--storm-gray-color);
  }
}
