.menu {
  &__list{
    display: flex;
    align-items: center;
    flex-direction: row;
  }

  &__item{
    position: relative;
    &:not(:last-child){
      margin-right: 40px;
    }
    &:last-child{
      margin-right: 30px;
    }
  }

  &__link{
    font-weight: 700;
    font-size: 16px;
    line-height: 160%;
    color: var(--gray-color);

    &::after{
      content: "";
      position: absolute;
      color: var(--storm-gray-color);
      background-color: var(--storm-gray-color);
      bottom: 0;
      left: 0;
      width: 0%;
      height: 2px;
      transition: width 0.3s ease-in-out;
    }

    &:hover{
      &::after{
        content: "";
        position: absolute;
        color: var(--storm-gray-color);
        background-color: var(--storm-gray-color);
        bottom: 0;
        left: 0;
        width: 100%;
        height: 2px;
      }
    }
  }
}
