.contacts-offices{
  padding-bottom: 60px;
  background-color: var(--bg-gray-color);

  &__content{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;

    @include tablet{
      flex-direction: column;
    }
  }

  &__map{
    width: 60%;
    height: 250px;
  }
}

.contacts-address{
  display: flex;
  flex-direction: column;

  &__item{
    display: inline-block;
    margin-bottom: 10px;
  }

  &__name{
    color: var(--storm-gray-color);
  }
}
