.team{
  padding-bottom: 120px;
  &__list{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 45px;

    @include small-tablet {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  &__item{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
  }

  &__top{
    position: relative;

    &:hover{
      .team__socials{
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &__socials{
    position: absolute;
    left: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0 0 4px 4px;
    padding: 20px 10px;
    height: 60px;
    width: 100%;
    background-color: rgba(#6CA589, 0.7);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }

  &__link {
    display: block;
    width: 20px;
    height: 20px;

    svg {
      display: block;
      width: 100%;
      height: 100%;
      fill: var(--color-light);
      object-fit: cover;
    }

    &:not(:last-child) {
      margin-right: 12px;
    }
  }

  &__img{
    width: 100%;
  }
}
