.works-main{
  padding-top: 120px;
  padding-bottom: 120px;

  &__list{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;

    @include tablet{
      grid-template-columns: repeat(2, 1fr);
    }

    @include small-tablet{
      grid-template-columns: repeat(1, 1fr);
    }
  }
}
