.section-works{
  &__top{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 45px;
  }
  &__title{
    max-width: 705px;
  }
  &__subtitle{
    margin: 0;
    margin-right: 40px;
  }

  &__item{
    width: 100%;
    height: 100%;
  }

  &__learn{
    margin-bottom: 125px;
  }

  &__slider{
    margin-bottom: 80px;

    @include tablet{
      margin-bottom: 0px;
    }
  }
}



//slider
.works-item{
    --img-height:311px;
    --change-margin:-68px;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    border-radius: 4px;
    box-shadow: 0 0 8px gray;
    background: var(--white-color);
    transition: transform 0.3s ease-in-out;

    @include minidesk{
      --img-height: 600px;
    }
  // &__i{
  //   background-repeat: no-repeat;
  //   background-position: center;
  //   background-size: cover;
  //   background-image: url('../img/work-1.jpg');
  //   height: 311px;
  //   transition: height 0.3s ease-in-out;
  // }

  &__image-container{
    height: var(--img-height);
    transition: height 0.3s ease-in-out;

    @include minidesk{
      height: calc(var(--img-height) + var(--change-margin));
    }
  }

  &__img{
    transition: height 0.3s ease-in-out;
    height: 100%;
    width: 100%;
  }

  &__content{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 10px;
    padding-top: 16px;
    padding-bottom: 23px;
  }

  &__title{
    margin: 0;
    margin-bottom: 5px;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: var(--dark-gray-color);
  }

  &__descr{
    display: inline-block;
    font-size: 14px;
    line-height: 150%;
    color: var(--storm-gray-color);
  }

  &__btn{
    margin-top: 24px;
    margin-bottom: var(--change-margin);
    border: 1px solid currentColor;
    border-radius: 4px;
    padding: 13px 31px;
    font-weight: 700;
    font-size: 14px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: var(--ten-color);
    transition: 0.3s ease-in-out;
    will-change: margin;

    &:hover{
      box-shadow: 0 0 8px var(--ten-color);
    }

    @include minidesk{
      margin-bottom: 0;
    }
  }

  &:focus {
    outline: 2px solid var(--color-primary);
    outline-offset: 2px;
  }

  &:hover {
    .works-item__btn {
      margin-bottom: 0;
    }

    // .works-item__i {
    //   height: calc(var(--img-height) + var(--change-margin));
    // }

    .works-item__image-container{
      height: calc(var(--img-height) + var(--change-margin));
    }
  }
}


