.partners{
  padding-top: 80px;
  background-color: var(--bg-gray-color);
  &__title{
    margin-bottom: 60px;
  }

  &__list{
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 102px;

    @include tablet{
      grid-template-columns: repeat(3, 1fr);
      gap: 60px;
    }
  }
}
