.hero{
  background-image: url('../img/bg.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: var(--gray-color);

  &_bg{
    background-color: #1e272c8e;
  }

  &__container{
    padding: 10px;
    max-width: 595px;
    padding-top: calc(220px - 92px);
  }

  &__btn{
    &:not(:last-child){
      margin-right: 24px;

      @include mobile{
        margin-right: 0;
        margin-bottom: 24px;
      }
    }

    @include mobile{
      margin: 0 auto;
      width: 90%;
    }
  }
}

.hero__buttons{
  padding-bottom: 200px;
}
