.offer{
  padding: 120px;

  @include small-tablet{
    padding: 0;
  }

  &__container{
    display: flex;
  }

  &__img-container{
    max-width: 50%;
    border-radius: 4px;
    margin-right: 50px;

    @include tablet{
      display: none;
    }
  }

  &__img{
    border-radius: 4px;
  }

  &__title{
    margin: 0;
    font-family: 'Lato';
    font-style: normal;
    font-weight: 900;
    font-size: 46px;
    line-height: 130%;
  }
}

.accordion{
  --padding-accordion: 50px;
  &__item{
    display: flex;
    flex-direction: column;

    &:not(:last-child){
      margin-bottom: 20px;
    }
  }

  &__btn{
    position: relative;
    display: flex;
    padding: 20px;
    padding-left: var(--padding-accordion);
  }

  &__title{
    font-weight: 700;
    font-size: 28px;
    line-height: 150%;
    color: var(--storm-gray-color);
  }

  &__icon{
    display: block;
    --width-icon:30px;
    --thickness:3px;
    position: absolute;
    left: 1px;
    display: block;
    width: var(--width-icon);
    height: var(--width-icon);
    top: 50%;
    transform: translateY(-50%);

    &::after{
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      border-radius: 100px;
      width: 100%;
      height: var(--thickness);
      background-color: var(--ten-color);
      transform: translateY(-50%);
    }
    &::before{
      content: "";
      position: absolute;
      left: 50%;
      top: 0;
      border-radius: 100px;
      width: var(--thickness);
      height: 100%;
      background-color: var(--ten-color);
      transform: translateX(-50%);
      transition: opacity 0.4s ease-in-out, transform 0.4s ease-in-out;
    }
  }

  &__body{
    max-height: 0;
    opacity: 0;
    overflow: hidden;
    padding-left: var(--padding-accordion);
    padding-right: 20px;
    transition: all 0s ease-in-out;

  }
}

.is-open{
  .accordion__body{
    opacity: 1;
    max-height: 100%;
    display: block;
    transition: all 1s ease-in-out;
  }

  .accordion__icon {
    &::before {
      transform: rotate(90deg);
      opacity: 0;
    }
  }
}
