
.about-list{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);

  &__item{
    padding: 5px;
    padding-left: 20px;
    margin-left: -20px;
    border-radius: 1px;

    &_first{
      background-color: var(--bg-oth-color);
      grid-column: 1;
    }

    &_second{
      margin-top: -20px;
      background-color: var(--primary-color);
      grid-row: 2;
      grid-column: 2;
    }
    &_third{
      margin-top: -20px;
      background-color: var(--bg-oth-color);
      grid-row: 3;
      grid-column: 3;
    }

    @include tablet{
      margin-top: 0px;
      grid-column: 2;

      &:not(:last-child){
        margin-bottom: 40px;
      }
    }
  }
}
