.facts{
  padding-top: 120px;

  &__title{
    margin-bottom: 60px;
  }

  &__list{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    max-width: 736px;
    gap: 48px;
    margin: 0 auto;

    @include small-tablet{
      grid-template-columns: repeat(2, 1fr);
    }

     @include mobile{
       grid-template-columns: repeat(1, 1fr);
     }
  }
}

.facts-item{
  &__circle{
    --progress-stroke: #000;
    --progress-bg-opacity: 0.3;

    display: flex;
    justify-content: center;
    align-items: center;

    margin: 0 auto;

    position: relative;
    margin-bottom: 20px;
    width: 148px;
    height: 148px;

    svg{
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
    }

    .progress {
      stroke: var(--progress-stroke);
      transform: rotate(-90deg) translateX(20px);
      transform-origin: center;
    }

    .progress-bg {
      stroke: var(--progress-stroke);
      opacity: var(--progress-bg-opacity);
    }

    &_one-color {
      --progress-stroke: #f89828;
    }

    &_two-color {
      --progress-stroke: #f52f6e;
    }

    &_three-color {
      --progress-stroke: #5a87fc;
    }

    &_four-color {
      --progress-stroke: green;
    }
  }

  &__value{
    font-weight: 700;
    font-size: 32px;
    line-height: 150%;

    color: var(--dark-gray-color);
  }

  &__info{
    display: block;
  }
}

