.new-container{
  max-width: 840px;
  padding: 0 15px;
  margin: 0 auto;
}

.new{
  --height-img: 400px;

  &__img-container{
    margin: 0 auto;
    max-width: 100%;
    max-height: var(--height-img);
    margin-top: -80px;
    border-radius: 4px;
  }

  &__img{
    border-radius: 4px;
    max-height: var(--height-img);
  }

  &__subtext{
    --new-left-padding:20px;
    --new-thick: 8px;

    display: block;
    position: relative;
    padding-left: 80px;

    &::after{
      position: absolute;
      content: "";
      top: 1px;
      border-top-left-radius: 8px;
      left: var(--new-left-padding);
      width: var(--new-thick);
      height: 30px;
      color: var(--ten-color);
      background-color: var(--ten-color);
    }

    &::before{
      position: absolute;
      content: "";
      top: 1px;
      border-top-left-radius: 8px;
      left: calc(var(--new-left-padding) + var(--new-thick) + 6px);
      width: var(--new-thick);
      height: 30px;
      color: var(--ten-color);
      background-color: var(--ten-color);
    }

  }
}
