// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Ubuntu", sans-serif;
  --content-width: 1200px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --white-color:#FFFFFF;
  --storm-gray-color:#787A80;
  --light-gray-color:#9A9CA5;
  --gray-color:#424551;
  --dark-gray-color:#1E212C;
  --ten-color:#6CA589;
  --primary-color:#77bf9c;
  --bg-gray-color:#F4F5F7;
  --bg-oth-color:rgba(248, 248, 255, 0.818);
  --bg-wrapper-color:#1e212c89;
  --athens-color:#f4f5f7;

  --padding-top-title: 143px;

  // sizes
  --grid-gap: 30px;

  // speed
  --hero-slider-speed: 500;

  // stylization
  --main-shadow:
    0 80px 80px -20px rgba(154, 156, 165, 0.08),
    0 30px 24px -10px rgba(154, 156, 165, 0.05),
    0 12px 10px -6px rgba(154, 156, 165, 0.04),
    0 4px 4px -4px rgba(30, 33, 44, 0.03);
}
