.contacts-request{
  padding-top: 121px;

  &__content{
    margin-bottom: 120px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    @include small-tablet{
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__img{
    border-radius: 4px;
    @include small-tablet{
      display: none;
    }
  }
}

.form-contacts{
  grid-column: 2/ span 2;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;

  @include small-tablet{
    grid-column: 1;
  }

  &__textarea{
    grid-column: 1/ span 2;
  }

  &__checkbox{
    display: flex;
    flex-direction: row;
    align-items: center;

    input{
      margin-right: 5px;

      &::after{
        color: black;
        background-color: black;
      }

      &::before{
        color: black;
        background-color: black;
      }
    }
  }
}
