.reviews{
  padding-top: 170px;
  background: #F4F5F7;

  &__container{
    display: flex;
  }

  &__content{
    position: relative;
    max-width: 600px;
    margin-bottom: 121px;
    margin-right: 135px;

    @include minidesk{
      margin-right: 20px;
    }
    @include tablet{
      max-width: 450px;
    }

    @include small-tablet {
      max-width: 100%;
    }
  }

  &__nav
  {
    z-index: 999;
    position: absolute;
    bottom: 0;
    right: 0;
    padding-bottom: 15px;
    padding-right: 10px;
  }

  &__main-img{
    position: relative;
    z-index: 5;
    margin-bottom: -60px;

    border-radius: 4px;
    width: 100%;
    object-fit: cover;

    @include minidesk {
      width: 380px;
      object-fit: cover;
      height: 400px;
    }

    @include tablet {
      width: 300px;
    }

    @include small-tablet {
      display: none;
    }
  }
}

.reviews-item{
  &__image-container{
    width: 72px;
    height: 72px;
    border-radius: 100%;
    margin-bottom: 24px;
  }

  &__img{
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__descr{
    margin-bottom: 24px;
  }

  &__author{
    display: inline-block;
    font-weight: 700;
    font-size: 16px;
    line-height: 160%;
    color: var(--dark-gray-color);
    margin-bottom: 4px;
  }

  &__post{
    display: block;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: var(--storm-gray-color);
  }
}
