.about-home{
  padding-bottom: 60px;
  &__container{
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}

.video-block{
  position: relative;
  overflow: hidden;
  border-radius: 4px;
  height: 500px;

  @include mobile {
    display: none;
  }

  &::before{
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 3;
    background-color: rgba(69, 75, 75, 0.495);
  }

  &__btn_play{
    display: flex;
    justify-content: center;
    align-items: center;

    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 10;
    border-radius: 100%;
    width: 80px;
    height: 80px;
    background-color: var(--ten-color);
    transform: translate(-50%, -50%);
    transition: background-color 0.3s ease-in-out, opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

    svg{
      width: 26px;
      height: 26px;
    }

    &:hover{
      background-color: var(--primary-color);
    }
  }

  &__btn_mute{
    display: flex;
    justify-content: center;
    align-items: center;

    position: absolute;
    left: 32px;
    bottom: 32px;
    z-index: 10;
    border-radius: 100%;
    width: 40px;
    height: 40px;
    background-color: var(--storm-gray-color);
    transition: background-color 0.3s ease-in-out, opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

    svg{
      width: 20px;
      height: 20px;
    }
  }

  &__video{
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}


.video{
  &_played{
    &::before{
      z-index: -1;
    }
  }
}

.btn-play{
  &_played{
    opacity: 0;
    pointer-events: none;
  }
}
