.services{
  padding-top: 120px;

  &__item{
    display: flex;
    margin-bottom: 60px;

    @include tablet{
      display: grid;
    }
  }

  &__img-container{
    width: 45%;
    &:nth-child(odd){
      padding-right: 45px;
    }

    @include tablet{
      width: 70%;
      padding-right: 0px !important;
      grid-row: 1;
      margin: 0 auto;
    }

    @include small-tablet{
      max-width: 90%;
    }
  }

  &__img{
    border-radius: 4px;
    object-fit: cover;
  }

  &__info{
    max-width: 45%;
    padding-top: 20px;
    &:nth-child(odd){
      padding-right: 45px;
    }

    @include tablet{
      max-width: 70%;
      padding-top: 10px;
      margin: 0 auto;
      margin-bottom: 40px;
    }

    @include small-tablet{
      max-width: 90%;
    }
  }

  &__title{
    margin: 0;
    margin-bottom: 24px;
    font-weight: 700;
    font-size: 32px;
    line-height: 150%;
  }

  &__descr{
    margin-bottom: 48px;

    @include tablet{
      margin-bottom: 10px;
    }
  }
}
