.new-hero{
  padding-top: 120px;
  padding-bottom: 120px;
  background-color: var(--bg-gray-color);

  &__content{
    margin-bottom: 60px;
  }

  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__meta{
    --meta-margin-right: 12px;
    *{
      position: relative;
      color: var(--storm-gray-color);

      &:first-child{
        color: var(--ten-color);
      }

      &:not(:last-child){
        margin-right: calc(var(--meta-margin-right)*2);
        &::after{
          content: "";
          position: absolute;
          color: var(--storm-gray-color);
          background-color: var(--storm-gray-color);
          top: 0;
          right: calc(-1 * var(--meta-margin-right));
          width: 1px;
          height: 100%;
        }
      }
    }
  }
}
