.know-form{
  position: relative;
  top: 92px;
  /* white */
  background: #FFFFFF;
  /* shadow/small */
  box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08), 0px 30px 24px -10px rgba(154, 156, 165, 0.05), 0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 4px 4px -4px rgba(30, 33, 44, 0.03);
  border-radius: 4px;
  padding: 48px 60px;

  &__content{
    display: flex;
    align-items: flex-end;

    @include tablet{
      flex-direction: column;
      align-items: center;
    }
  }

  &__caption{
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;

    color: var(--gray-color);
  }

  &__input{
    padding-top: 11px;
    padding-bottom: 12px;
    padding-left: 16px;
    padding-right: 10px;

    font-weight: 400;
    font-size: 14px;
    line-height: 150%;

    color: #9A9CA5;

    background: #F4F5F7;
    border: 1px solid #D7DADD;
    border-radius: 4px;

    &_big{
      width: 100%;
    }
  }

  &__btn{
    @include tablet{
      margin-top: 20px
    }
  }
}

.form-field{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-right: 24px;
  &_big{
    flex-grow: 2;
  }

  @include tablet{
    margin-right: 0;
  }
}
