.comments{
  padding-top: 60px;
  margin-bottom: 60px;
  &__title{
    text-align: center;
  }

  &__item{
    display: flex;

    @include mobile{
      flex-direction: column;
    }
  }

  &__left{
    display: flex;
    flex-direction: column;
    width: 400px;
    margin-right: 30px;
  }

  &__author{
    width: 100%;
    font-weight: 700;
    font-size: 18px;
    line-height: 150%;
  }

  &__right{
  }

  &__text{
    margin: 0;
    padding: 0;
  }
}
