.footer{
  padding-top: 80px;
  padding-bottom: 80px;
  background-color: var(--dark-gray-color);
  color: white;

  &__top{
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;

    @include small-tablet{
      flex-direction: column;
    }
  }

  &__left{


    &-links{
      display: flex;
      margin-right: 15px;

      @include tablet{
        flex-direction: column;
        margin-bottom: 20px;
      }
    }

    @include small-tablet{
      margin-bottom: 40px;
      margin-left: 0;
    }
  }

  &__footer-right{

  }

  &-form{
    &__items{
      display: flex;
      align-items: center;
      margin-bottom: 12px;

      @include small-tablet{
        flex-direction: column;
        align-items: flex-start;
      }
    }

    &__field{
      margin-right: 20px !important;
      @include small-tablet{
        margin-bottom: 20px;
      }
    }

    &__title{
      margin: 0;
      margin-bottom: 15px;
      font-weight: 700;
      font-size: 24px;
      line-height: 150%;
    }

    &__descr{
      margin: 0;
      opacity: 0.6;
    }
  }

  &__descr{
    margin: 0;
    opacity: 0.6;
  }

  &__info{
    display: grid;
    margin-bottom: 60px;
    grid-template-columns: 1fr 2fr;
    gap: 210px;

    @include small-tablet{
      grid-template-columns: repeat(1, 1fr);
      gap: 40px;
    }
  }

  &__copyright{
    margin: 0;
    font-weight: 400;
    font-size: 12px;
    line-height: 150%;
  }
}

.footer-address{
  &__title{
    margin: 0;
    margin-bottom: 12px;
  }

  &__item{
    display: inline-block;
    margin-bottom: 4px;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    color: rgba(255, 255, 255, 0.642);

    a{
      transition: color 0.3s ease-in-out;
      color: rgba(255, 255, 255, 0.642);
    }
    a:hover{
      color: var(--primary-color);
    }
  }

  &__name{
    color: var(--white-color);
  }
}

.footer-nav{
  display: grid;
  grid-template-columns: repeat(3, 1fr);

  &__about:not(:last-child){
    margin-right: 20px;
  }

  &__title{
    margin: 0;
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    color: white;
  }

  &__link{
    transition: color 0.3s ease-in-out;
    &:hover{
      color: var(--primary-color);
    }
  }
}


.social{
  display: flex;
  flex-direction: row;
  align-items: center;


  &__item{
    width: 24px;
    height: 24px;
    svg{
      max-width: 100%;
      max-height: 100%;
      transition: fill 0.3s ease-in-out;
      fill: var(--white-color);
    }

    &:not(:last-child){
      margin-right: 8px;
    }

    &:hover{
      svg{
        fill: #8fffc9;
      }
    }
  }
}
