.values{
  &__list{
    --mission-gap: 94px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: calc(var(--mission-gap)*2);

    @include tablet{
      grid-template-columns: repeat(1, 1fr);
      gap: 0;
      margin: 0 auto;
    }
  }
  &__item{
    max-width: 285px;
    min-height: 180px;
    padding-top: 80px;
    background-position: top center;
    background-repeat: no-repeat;

    position: relative;
    transition: all 0.8s ease 0s;
    transform-style: preserve-3d;
    text-align: center;
    &:not(:last-child){
      &::after{
        content: "";
        position: absolute;
        color: black;
        background-color: black;
        top: 0;
        right: calc(-1 * var(--mission-gap));
        width: 1px;
        height: 100%;
        background-image: url("../img/divider.svg");
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
      }
    }

    @include tablet{
      padding-top: 40px;
      margin: 0 auto;
      margin-bottom: 20px;

      &::after{
        display: none;
      }
    }

    &_durability{
      background-image: url('../img/svg/like.svg');
    }

    &:hover .values__body{
      transform: rotateY(-180deg);
    }
  }

  &__body{
    width: 100%;
    height: 100%;
    transition: all 0.8s ease 0s;
    transform-style: preserve-3d;
    text-align: center;
  }

  &__card{
    padding-top: 10px;
    width: 100%;
    height: 100%;
    &_front{
      background-color: rgba(154, 156, 165,  0.236);
      box-shadow: 0px 80px 80px -20px rgba(154, 156, 165,  0.236), 0px 30px 24px -10px rgba(154, 156, 165, 0.236), 0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 4px 4px -4px rgba(30, 33, 44, 0.03);
      position: absolute;
      z-index: 2;
      transform: rotateY(0deg);
      backface-visibility: hidden;
    }

    &_back{
      box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08), 0px 30px 24px -10px rgba(154, 156, 165, 0.05), 0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 4px 4px -4px rgba(30, 33, 44, 0.03);
      transform: rotateY(-180deg);

      backface-visibility: hidden;
    }
  }


  &__subtitle{
    text-align: center;
    margin: 0;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    margin-bottom: 8px;
  }

  &__descr{
    text-align: center;
    color: var(--dark-gray-color);
    margin: 0;
  }
}
