.header {
  padding-top: 20px;
  padding-bottom: 18px;
  background-color: var(--white-color);

  &__container {
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
  }

  &__menu{
    @include minidesk {
      margin-left: 0;
    }

    @include tablet {
      position: fixed;
      left: 0;
      top: 0;
      z-index: 1000;
      display: block;
      overflow-y: auto;
      padding: 50px;
      width: 80%;
      height: 100vh;
      background-color: var(--white-color);
      transform: translateX(-150%);
      transition: transform 0.3s ease-in-out;

      &--active {
        transform: none;

        .menu__list{
          display: flex;
          flex-direction: column;
          align-items: flex-start;
        }

        .menu__item{
          margin-bottom: 20px;
        }

        .header-contacts__link{
          margin-bottom: 20px;
        }
      }
    }

    @include mobile {
      padding: 20px;
    }
  }
}

.logo{
  margin-right: 20px;
}


