.work-main{
  background-color: var(--bg-gray-color);
  padding-top: 30px;
  padding-bottom: 150px;

  &__left {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    background-color: var(--white-color);
    grid-column: 6 span;
    padding: 24px;
    padding-top: 24px;
  }

  &__right {
    padding-top: 24px;
    display: flex;
    justify-content: flex-end;
    grid-column: 6 span;

    @include tablet {
      justify-content: center;
    }
  }
}

.work-info{
  border-radius: 4px;
  padding: 25px 60px;
  padding-right: 40px;
  width: 495px;
  min-height: 300px;
  box-shadow: var(--main-shadow);
  background-color: var(--white-color);

  &__item {
    display: flex;

    &:not(:last-child) {
      margin-bottom: 17px;
    }
  }

  &__category {
    flex-shrink: 0;
    width: 130px;
    font-weight: 700;
    font-size: 16px;
    line-height: 160%;
    text-transform: uppercase;
    color: var(--dark-gray-color);
  }

  &__value {
    font-weight: 700;
    font-size: 16px;
    line-height: 160%;
    color: var(--storm-gray-color);
  }

  &__btn{
    display: block;
    margin: 0 auto;
  }

  @include small-tablet {
    width: 100%;
  }

  @include mobile {
    padding: 10px 0;
  }
}

.grid{
  display: grid;
  grid-template-columns: repeat(12, 1fr);

  @include tablet{
    grid-template-columns: repeat(1, 1fr);
  }
}
