.process {
  padding-top: 30px;
  padding-bottom: 80px;

  &__title {
    margin-bottom: 60px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    counter-reset: section;
    gap: 30px;

    @include tablet{
      grid-template-columns: repeat(2, 1fr);
    }

    @include small-tablet{
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;

    &::before {
      counter-increment: section;
      content: "0"counter(section);
      margin-bottom: 18px;
      font-weight: 700;
      font-size: 46px;
      line-height: 130%;
      color: #d7dadd;
    }

    &:not(:last-child) {
      &::after {
        content: "";
        position: absolute;
        right: 0;
        top: 30px;
        width: 75%;
        height: 1px;
        background-image: url("../img/services/process-line.svg");
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;

        @include tablet {
          display: none;
        }
      }
    }

    @include tablet {
      margin-bottom: 20px;
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 9px;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: var(--dark-gray-color);
  }

  &__descr {
    margin: 0;
    font-size: 16px;
    line-height: 160%;
    color: var(--storm-gray-color);
  }
}
