.section-services{
  padding-top: 220px;
  padding-bottom: 80px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url('../img/bg-service-section.png');

  &__list{
    margin-bottom: 80px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;

    @include tablet{
      grid-template-columns: repeat(2, 1fr);
    }

    @include mobile{
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__item{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;


    background-color: var(--white-color);
    border: 1px solid #E5E8ED;
    box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08), 0px 30px 24px -10px rgba(154, 156, 165, 0.05), 0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 4px 4px -4px rgba(30, 33, 44, 0.03);
    border-radius: 4px;
  }

  &__link{
    position: relative;
    width: 100%;
    height: 100%;

    padding: 0 10px;
    padding-top: 87px;
    padding-bottom: 90px;

    &::before{
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 3;
      border-radius: inherit;
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
      background-image: url('../img/service-section-bg1.jpg');
      background-color: var(--dark-gray-color);
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
    }

    &::after{
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      z-index: 5;
      border-radius: inherit;
      background-color: rgba(#1e212c, 0.6);
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
    }

    &:hover{
      &::after, &::before{
        opacity: 1;
      }

      .services-item__svg{
        fill: var(--white-color);
      }

      .services-item__title{
        color: var(--white-color);
      }
    }
  }

  &__subtitle{
    margin: 0;
    margin-right: 40px;
  }
}

.services-item{
  &__content{
    position: relative;
    z-index: 10;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  &__svg{
    margin-bottom: 26px;
    width: 52px;
    height: 52px;
    fill: var(--ten-color);
    transition: fill 0.3s ease-in-out;

    & path{
      transition: fill 0.3s ease-in-out;
    }
  }

  &__title{
    transition: color 0.3s ease-in-out;
    text-align: center;
  }
}
