.header-contacts{
  display: flex;
  flex-direction: row;

  &__link {
    &:not(:last-child) {
      margin-right: 35px;
    }
  }

  @include tablet {
    flex-direction: column;
    display: none;
  }

  &--nav {
    display: none;

    @include tablet {
      display: block;
    }
  }

  &__link{
    display: flex;
    flex-direction:column;
    align-items: flex-start;

    &:not(:last-child){
      margin-right: 20px;
    }

    &_tel{
      background-image: url("../img/svg/iPhone.svg");
    }
    &_mail{
      background-image: url("../img/svg/Chat.svg");
    }
  }
}

.contacts-link{
  padding-left: 45px;
  background-position: left center;
  background-repeat: no-repeat;
  background-size: 40px 40px;

  &__action{
    font-weight: 700;
    font-size: 14px;
    line-height: 150%;
    color: var(--gray-color)
  }

  &__value{
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    color: var(--dark-gray-color);
  }
}
