.news-main{
  padding-bottom: 120px;

  &__list{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 60px;

    @include small-tablet{
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__img{
    border-radius: 4px;
  }
}

.news-list{
  &__item{
    box-shadow: var(--main-shadow)
  }

  &__headline{
    display: block;
    margin: 0;
    margin-bottom: 12px;

    transition: color, 0.3s ease-in-out;
    &:hover{
      color: var(--ten-color);
    }
  }

  &__meta{
    --meta-margin-right: 12px;
    margin-bottom: 16px;
    span{
      position: relative;
      color: var(--storm-gray-color);

      &:not(:last-child){
        margin-right: calc(var(--meta-margin-right)*2);
        &::after{
          content: "";
          position: absolute;
          color: var(--storm-gray-color);
          background-color: var(--storm-gray-color);
          top: 0;
          right: calc(-1 * var(--meta-margin-right));
          width: 1px;
          height: 100%;
        }
      }
    }
  }

  &__descr{
    color: #424551;
  }

  &__learn{
    margin-bottom: 120px;
  }
}
