.news-filter{
  &__title{
    margin-bottom: 40px;
  }

  &__form{
    margin-bottom: 60px;
  }

  &__list{
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 10px;

    @include small-tablet{
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__button{
    pointer-events: all;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 10px;
    width: 100%;
    color: var(--light-gray-color);
    font-weight: 700;
    font-size: 16px;
    transition: color 0.3s ease-in-out;
    cursor: pointer;

    @include small-tablet{
      max-width: 250px;
      margin: 0 auto;
    }

    &:hover{
      color: var(--ten-color);
    }

    &_pressed{
      color: var(--ten-color);
      border-style: solid;
      border-color: var(--primary-color);
      border-width: 2px;
      border-radius: 4px;
    }

    input{
      display: none;
    }
  }
}
